.navigationOptions {
  width: 100%;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.titlePosition {
  width: 100%;
  align-items: center;
  display: flex;
}


@media (max-width: 600px) {
  .MuiDrawer-root > .MuiPaper-root {
    top: 56px !important;
  }
}

.MuiDrawer-root > .MuiPaper-root {
  top: 64px;
}

/* This isn't mobile friendly but for a demo should be fine */
.mainContent {
  padding-left: 250px;
  padding-right: 35px;
  padding-top: 25px;
}

.sectionHeader {
  font-size: 1.25rem !important; /* overwrite React Material UI */
}

.headerLink {
  color: white;
  text-decoration: none;
}

.sideNav > div {
  min-width: 200px;
}

.sideNavLink {
  color: inherit;
  text-decoration: inherit;
}